import styled from 'styled-components';

const Wrapper = styled.div`
  text-align: center;
  margin: -45px -60px;
  
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  
  & > a{
    min-width: 39%;
    max-width: 39%;
    
    img, & > div {
      border-radius: 5px;
    }
  }
  
  & > a ~ .right-desc{
    padding-left: min(30px, 2vw);
  }
  
  & > a > .no-thumbnail{
    width: 100%;
    height: 85%;
    background: rgba(115,210,238,0.9);
    background: linear-gradient(45deg, rgba(115,210,238,0.9) 0%, rgba(15,116,175,0.9) 100%);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 45px;
    font-weight: 900;
    text-shadow: #111 1px 1px 9px;
    letter-spacing: -3px;
    padding-bottom: 5px;
    color: white;
  }
  
  & > .right-desc{
    min-width: 61%;
    max-width: 61%;
    
    display: flex;
    flex-direction: column;
    
    justify-content: flex-start;
    align-items: flex-start;
    
    & > * {
      margin-left: unset;
      padding-left: unset;
    }
    
    & > h1{
      text-align: start;
      line-height: 1.1;
    }
    
    & > h2{
      margin: 9px 0 10px;
    }
    
    & > p {
     margin: 10px 0;
     text-align: start;
    }
    
    & > a { 
      margin-top: 5px;
    }
    
    & > h1 > a{
      font-size: 90%;
      display: flex;
      justify-content: center;
      align-items: center;
    
      svg{
        margin-left: 6px
      }
    }
    
    & > *:not(h1){
      padding-left: 2px;
    }
  }
  
  @media only screen and (max-width: 870px) {
    margin: 0;
   
    & > a{
      margin-top: -30px;  
      margin-bottom: -30px;   
      img, & > div {
        border-radius: 3px;
      }
    }
    
    & > .right-desc{  
      margin-top: -30px;
      margin-bottom: -30px;
      & > * {
        margin-right: unset;
        padding-right: unset;       
      }
      
      & > p {
        display: none;
      }
      
      & > h1{
        width: 100%;
      }
      
      & > h1 > a{
       font-size: 70%;
       position: relative;
       
       width: 100%;
       
       justify-content: flex-start;
       align-items: flex-start;
       
       svg {
         position: absolute;
         right: 0;
       }
      } 
       
      & > h2{
       font-size: 12px;
       margin: 5px 0;
      }
 
      & > span{
        margin-bottom: 2px;
        font-size: 12px;
      }      
    }
    
    & > a > .no-thumbnail{
      max-height: min(100px, 12.5vh);
      font-size: 25px;
    }
    
    & > a ~ .right-desc{
      padding-left: 10px;
    }
   
  }
`;

export default Wrapper;